function FrameHTML({ index, offset, id, artist, title, materials, size }) {
    const divStyle = {
        position: 'relative',
        margin: '20px',
        marginTop: '200px',
        width: '100vw',
        // height: '100vh',
    };

    const setCurrentFrame = useGlobalStore(state => state.setCurrentFrame);
    const setCaption = useGlobalStore(state => state.setCaption);

    const el = (
        <>
            <div>{id}</div>
            <div>{artist}</div>
            <div>{title}</div>
            <div>{materials}</div>
            <div>{size}</div>
        </>
    );

    const { ref, inView } = useInView({
        threshold: 0,
        onChange: (_inView, entry) => {
            _inView && setCurrentFrame(index);
            _inView && setCaption(el);
        }
    });

    return (
        <div style={divStyle}>
            <div ref={ref} style={{
                position: 'sticky',
            }}>
                {el}
                <div style={{
                    width: "50%",
                    // overflow: 'scroll'
                }}>
                    <Statement artist={artist} />
                </div>
            </div>
        </div>
    );
}

export function Frames({ props }) {
    const frameElements = useMemo(() => {
        const artist2ids = {};
        const id2frames = {};

        FRAMES.forEach(frame => {
            const artist = frame.artist.split(',')[0];
            if (artist in artist2ids) {
                artist2ids[artist].push(frame.id);
            } else {
                artist2ids[artist] = [frame.id];
            }

            id2frames[frame.id] = frame;
        });

        return artistsOrder.map(artist => {
            return artist2ids[artist].map(id => id2frames[id]);
        }).flat();
    }, []);

    return (
        <div>
            {frameElements.map((frame, index) => (
                <FrameHTML
                    key={index}
                    index={index + 3}
                    offset={3}
                    {...frame}
                />
            ))}
        </div>
    );
}

