import { useGlobalStore } from "Store";

export const Preface = () => {
    const language = useGlobalStore(state => state.language);
    const text = require(`assets/preface/${language}/index.html`).default;

    return (
        <div
            style={{
                width: "50vw",
                padding: "20px"
            }}
            className="container"
            dangerouslySetInnerHTML={{ __html: text }}
        >
        </div>
    );
}
